
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'

import Progress from 'c%/progress'
export default{
mixins: [common,globals],
components: {
Card,
Progress
},
data () {
return {
data: []
}
},
mounted () {

},
methods: {
serviceDetail (item) {
this.$router.push({path:'./service/'+item.cid})
},
getTeachPercent (row) {
return (row.attendance_times / row.arrange_times) * 100
}
},
computed: {
data_uri () {
return 'classes?with=lesson,teacher,assistant&teach_eid='+this.eid$
}
}
}



